.ds-form-label {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  display: inline-block;
  font-family: "3dslight";
  font-size: 1.0625rem;
  color: rgba(0, 0, 0, 0.35);
  border: 0 none;
}

.ds-form-checkbox,
.ds-form-radio {
  display: none;
}

.ds-form-label-checkbox {
  position: relative;
}

.ds-form-label-checkbox:after,
.ds-form-label-checkbox:before {
  content: "";
  display: inline-block;
  cursor: pointer;
  position: absolute;
  margin: 0px;
  padding: 0px;
}

.ds-form-label-checkbox:after {
  left: 0;
  border: 2px solid black;
  width: 17px;
  height: 17px;
}

.ds-form-label-checkbox:before {
  width: 9px;
  height: 5px;
  top: 4px;
  left: 4px;
  border: 3px solid black;
  border-top: none;
  border-right: none;
  background: transparent;
  opacity: 0;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  background: red;
  z-index: 1000;
}

.ds-form-label-radio {
  position: relative;
}

.ds-form-label-radio:after,
.ds-form-label-radio:before {
  content: "";
  display: inline-block;
  cursor: pointer;
  position: absolute;
  margin: 0px;
  padding: 0px;
}

.ds-form-label-radio:after {
  left: 0;
  border: 2px solid black;
  border-radius: 100%;
  width: 17px;
  height: 17px;
}

.ds-form-label-radio:before {
  left: 2px;
  top: 2px;
  width: 15px;
  height: 15px;
  background: red;
  border-radius: 100%;
}

.ds-form-switch-checkbox {
  display: none;
  opacity: 0;
}

.ds-form-switch-label {
  display: block;
  font-size: 10px;
  padding-left: 65px;
  position: relative;
  color: rgba(0, 0, 0, 0.35);
  font-family: "3dslight";
}

.ds-form-switch-label:before,
.ds-form-switch-label:after {
  content: "";
  display: inline-block;
  cursor: pointer;
  position: absolute;
  margin: 0px;
  padding: 0px;
}

.ds-form-switch-label:before {
  -webkit-transition: all 0.2s ease;
  -moz-transition: all 0.2s ease;
  transition: all 0.2s ease;
  width: 27px;
  height: 27px;
  background: #ffffff;
  border-radius: 27px;
  top: 2px;
  left: 2px;
  z-index: 10;
}

.ds-form-switch-label:after {
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  transition: all 0.5s ease;
  width: 50px;
  height: 31px;
  background: rgba(0, 0, 0, 0.35);
  border-radius: 27px;
  top: 0px;
  left: 0px;
  z-index: 9;
}

.ds-form-switch-checkbox:checked + .ds-form-switch-label::before {
  left: 21px;
}

.ds-form-switch-checkbox:checked + .ds-form-switch-label::after {
  background: #239DE8;
}

.ds-form {
  display: block;
  margin: 10px 0;
}

.ds-form-fieldset {
  border: 0;
  padding: 0;
  margin: 10px 0;
  background: #ffffff;
}

.ds-form-fieldset .ds-form-input-text:focus,
.ds-form-fieldset .ds-form-input-text:invalid,
.ds-form-fieldset .ds-form-select {
  box-shadow: none;
  outline: none;
}

.ds-form-fieldset .ds-form-group-element {
  border: solid 1px rgba(0, 0, 0, 0.12);
  padding: 24px;
}

.ds-form-fieldset .ds-form-group-element.no-border {
  border: 0;
}

.ds-form-fieldset .ds-form-group-element .ds-form-select,
.ds-form-fieldset .ds-form-group-element .ds-form-input-text {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  display: block;
  width: 100%;
  font-family: "3dslight";
  font-size: 1.0625rem;
  color: rgba(0, 0, 0, 0.35);
  background-color: #ffffff;
  border: 0 none;
}

.ds-form-fieldset .ds-form-group-element.ds-form-group-element:first-child {
  border-top-right-radius: 25px;
  border-top-left-radius: 25px;
}

.ds-form-fieldset .ds-form-group-element.ds-form-group-element:last-child {
  border-bottom-right-radius: 25px;
  border-bottom-left-radius: 25px;
}

.ds-form-fieldset .ds-form-group-element.ds-form-group-element + .ds-form-fieldset .ds-form-group-element.ds-form-group-element:last-child {
  border-bottom-right-radius: 25px;
  border-bottom-left-radius: 25px;
}

.ds-form-fieldset .ds-form-group-element + .ds-form-group-element {
  border-radius: 0;
  border-top: 0;
}

.ds-form-error {
  position: relative;
  display: block;
  width: 100%;
  padding: 16px 24px;
  background: #e57373;
  color: #ffffff;
  font-size: 14px;
  font-family: "3dslight";
  text-align: left;
  margin: 24px 0 -24px -24px;
  -webkit-transition: 1s ease all;
  -moz-transition: 1s ease all;
  transition: 1s ease all;
}

.ds-form-error:after {
  position: absolute;
  content: " ";
  right: 8px;
  top: -20.5px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 20.5px 20px 20.5px;
  border-color: transparent transparent #e57373 transparent;
}

.ds-form-error:before {
  position: absolute;
  content: " ";
  right: 22px;
  top: -40px;
  width: 12px;
  height: 12px;
  background: url("../icon/shape.svg") center center no-repeat;
}

.ds-btn {
  display: inline-block;
  font-weight: normal;
  text-decoration: none;
  text-transform: uppercase;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  border-radius: 100px;
  cursor: pointer;
  color: #ffffff;
  border: 2px solid #239DE8;
  font-size: 17px;
  font-family: "3dsregular";
  padding: 16px 24px;
  background: #239DE8;
}